import {useRef, useEffect} from 'react';
import { Container, Stack, Box, Typography } from '@mui/material';
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

import ItemDiv from "../components/ItemDiv.js";
import ImageWithSkeleton from '../components/ImageWithSkeleton.js';


const flexStyle = {
    padding: 2,
    display: "flex",
    flexFlow: "row wrap",
    justifyContent: "center",
    alignItems: "center",
    alignContent: "spaceEvenly",
    maxWidth: {xs: 350, sm:350, md: 800}
}

const aboutProfiles = [
    {
        imgSource: "/images/profile_pic.webp",
        imgAlt: "Profile Picture",
        title: "Thien Tran",
        subtitle: "UI/UX Developer",
        body: "Hello, I'm a UI/UX Developer from Adelaide, Australia. I have a diverse skillset that allows me to approach web design in a more holistic manner. I am passionate about learning and am always looking for projects that will give me the opportunity to engage with more web technologies. In my free time and I enjoy playing social Soccer or Basketball. I also enjoy playing and designing boardgames.",
    }
]

gsap.registerPlugin(ScrollTrigger);

const About = () => {
    const profileRef = useRef(null);
    
    useEffect(() => {
        
        let ctx = gsap.context(() => {
            
            gsap.from(".profile", 
            {   
                scrollTrigger: {
                    trigger: ".profile",
                    toggleActions: "restart none none none"
                },
                opacity: 0,
                y: 70,
                duration: 3,
                ease: "circ.out"
            })
        
        }, profileRef)
        
        return () => ctx.revert();

    }, []);

    return (
        <Container component="section" ref={profileRef} sx={{pt:38, pb:14}} maxWidth="xs" fixed>
            <Stack direction="column" alignItems="center" spacing={3}>
                <Typography variant="h3" component="h4" align="center" gutterBottom>About</Typography>
                <ItemDiv sx={{height:3, width:40, padding:0}} darkhex="#AE7EAA" lighthex="#ff97cc"/>
                {aboutProfiles.map((item, i) => {
                    return (
                            <ItemDiv key={i} sx={flexStyle} className="profile">
                                <ImageWithSkeleton source={item.imgSource} text={item.imgAlt} width={250} height={250}/>
                                <Box sx={{flexBasis:"60%", flexGrow:"2", padding:2}}>
                                    <Typography variant="h6" component="h5" gutterBottom>{item.title}</Typography>
                                    <Typography variant="subtitle2" gutterBottom>{item.subtitle}</Typography>
                                    <Typography variant="body2" gutterBottom>{item.body}</Typography>
                                </Box>
                            </ItemDiv>
                    )
                })}
            </Stack>
        </Container>
    )
}

export default About